import { Container, Grid, Typography } from '@mui/material'
// import { Animation } from 'react-animate-style'
import React, { useState } from 'react'
import { ContactUsAccordian } from './ContactUsAccordian'
import { sendSESMail } from '../../services/MailService'
const regexEmail = /^[^\s@]+@[^\s@]+\.[^\s@]+$/

export const ContactUsContent = () => {
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [message, setMessage] = useState('')
    const [isInValidEmail, setIsInValidEmail] = useState(false)
    const [submit, setSubmit] = useState(false)

    const sendMail = async () => {
        setSubmit(true)
        const subject = 'Get in touch'
        if (email === "" || !regexEmail.test(email)) {
            setIsInValidEmail(true)
        } else {
            setIsInValidEmail(false)
        }
        if (name === "" || isInValidEmail || message === "") {
            return
        }
        const template = `<p>Dear Agrivimaan, ,<br/>
        Mr/Mrs <b>${name}</b> Requesting to touch using email "<b>${email}</b>
        for <b>${message}</b></p>`
        console.log(subject, template)
        await sendSESMail(subject, template)
        setSubmit(false)
        setName('')
        setEmail('')
        setMessage('')
    }
    const enterEmail = (e) => {
        setEmail(e.target.value)
        if (!regexEmail.test(e.target.value)) {
            setIsInValidEmail(true)
        } else {
            setIsInValidEmail(false)
        }
    }
    return (
        <div>
            <Container maxWidth='lg'>
                <Grid paddingY={10}>
                    <Typography textAlign={'center'} className='ff-mo fw-bold' marginTop={'5'} color={'#1D69E7'}>CONTACT US</Typography>
                    <Grid container display={'flex'} justifyContent={'center'}>
                        <Grid xs={12} sm={9} md={6}>
                            <Typography textAlign={'center'} className='ff-mi' variant='h4' marginY={2}>CROP WINGS Contact</Typography>
                            <div className='d-flex justify-content-start' style={{ marginTop: -100, marginLeft: 65 }}><img src="assets/element-blur-2 1.png" alt="" height={150} /></div>
                        </Grid>
                    </Grid>
                    <Grid container display={'flex'} justifyContent={'space-around'} mt={5}>
                        <Grid xs={12} sm={6} md={4} marginY={3}>
                            {/* <Animation className='h-100' animationIn="fadeInLeftBig" animationOut='backInDown' isVisible={true} animationInDelay={1000}> */}
                            <div className='card border-0 bg-secondary bg-opacity-25 p-2 m-3 rounded-4 h-100 shadow'>
                                <div className='d-flex justify-content-between' style={{ marginTop: -50 }}>
                                    <div className='d-flex justify-content-center align-items-center bg-primary rounded-circle ms-3' style={{ height: 80, width: 80 }}>
                                        <img src="assets/icons/Group (6).png" alt="" />
                                    </div>
                                </div>
                                <div className='card-body'>
                                    <Typography variant='h6' className='ff-mo' fontWeight={'bold'}>CROPWINGS Email</Typography>
                                    <Typography color={'#656565'} className='ff-mo'>cropwings@gmail.com</Typography>
                                </div>
                            </div>
                            {/* </Animation> */}
                        </Grid>
                        <Grid xs={12} sm={6} md={4} marginY={3} >
                            {/* <Animation className='h-100' animationIn="fadeInLeftBig" animationOut='backInDown' isVisible={true} animationInDelay={500}> */}
                            <div className='card border-0 bg-secondary bg-opacity-25 p-2 m-3 rounded-4 h-100 shadow'>
                                <div className='d-flex justify-content-between' style={{ marginTop: -50 }}>
                                    <div className='d-flex justify-content-center align-items-center bg-primary rounded-circle ms-3' style={{ height: 80, width: 80 }}>
                                        <img src="assets/icons/Group 1.png" alt="" />
                                    </div>
                                </div>
                                <div className='card-body'>
                                    <Typography variant='h6' className='ff-mo' fontWeight={'bold'}>CROPWINGS Phone</Typography>
                                    <Typography color={'#656565'} className='ff-mo'>+91 853 546 0665, +91 741 137 7836, <br />+91 789 999 1769</Typography>
                                </div>
                            </div>
                            {/* </Animation> */}
                        </Grid>
                        <Grid xs={12} sm={6} md={4} marginY={3}>
                            {/* <Animation className='h-100' animationIn="fadeInLeftBig" animationOut='backInDown' isVisible={true}> */}
                            <div className='card border-0 bg-secondary bg-opacity-25 p-2 m-3 rounded-4 h-100 shadow'>
                                <div className='d-flex justify-content-between' style={{ marginTop: -50 }}>
                                    <div className='d-flex justify-content-center align-items-center bg-primary rounded-circle ms-3' style={{ height: 80, width: 80 }}>
                                        <img src="assets/icons/g2505.png" alt="" />
                                    </div>
                                </div>
                                <div className='card-body'>
                                    <Typography variant='h6' className='ff-mo' fontWeight={'bold'}>Office Location</Typography>
                                    <Typography color={'#656565'} className='ff-mo'>Adarsha colony, Sindhanur 584 128</Typography>
                                </div>
                            </div>
                            {/* </Animation> */}
                        </Grid>
                    </Grid>
                </Grid>
            </Container>
            <Grid container>
                <Grid container xs={12} md={8} bgcolor={'#0F2946'} sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                    <Grid xs={11} sm={6}>
                        <Typography className='ff-mo fw-bold' marginTop={'5'} color={'#1D69E7'}>Lets Connect</Typography>
                        <Typography className='ff-mi' variant='h4' marginY={2} color={'white'}>Get in touch</Typography>
                        <img src="assets/element-blur-2 1.png" alt="" className='blur-section-6' />
                        <Typography color={'white'} className='ff-mo ts-12'>
                            Our team of experts will be happy to provide you with detailed information, answer your questions, and discuss how our agri drone technology can benefit your specific needs.
                        </Typography>
                        <div>
                            <div className="mb-3 mt-4">
                                <input type="text" value={name} onChange={(e) => { setName(e.target.value) }} className={`form-control rounded-pill ${submit && !name ? 'is-invalid' : ''}`} placeholder="Name" />
                            </div>
                            <div className="mb-3">
                                <input type="email" value={email} onChange={enterEmail} className={`form-control rounded-pill ${submit && isInValidEmail ? 'is-invalid' : ''}`} placeholder="Email" />
                            </div>
                            <div className="mb-3">
                                <textarea value={message} onChange={(e) => { setMessage(e.target.value) }} className={`form-control rounded-4 ${submit && !message ? 'is-invalid' : ''}`} rows="3" placeholder='Message'></textarea>
                            </div>
                            <div className="mb-3">
                                <button className="form-control btn btn-primary rounded-pill" onClick={sendMail}>SEND</button>
                            </div>
                        </div>
                    </Grid>
                </Grid>
                <Grid xs={12} md={4}>
                    <img src="assets/Frame 35.png" alt="" width={'100%'} height={'100%'} />
                </Grid>
            </Grid>
            <Container maxWidth='md'>
                <Grid container display={'flex'} alignItems={'center'} py={7}>
                    <Grid sm={12} md={6}>
                        <Typography className='ff-mo fw-bold' marginTop={'5'} color={'#1D69E7'}>FAQ</Typography>
                        <Typography className='ff-mi' variant='h4' marginY={2}>Frequently Asked Questions</Typography>
                        <img src="assets/element-blur-2 1.png" alt="" className='blur-section-6' />
                        <ContactUsAccordian />
                    </Grid>
                    <Grid container sm={12} md={6} display={'flex'} justifyContent={'center'}>
                        <Grid container sm={12} md={9} display={'flex'} justifyContent={'center'}>
                            <Grid padding={1} xs={9}>
                                <div className='d-flex justify-content-end'>
                                    <img src="assets/element-ellipse-1-300x300 1.png" alt="" style={{ height: 100, marginBottom: -85, marginRight: -45 }} />
                                </div>
                                <div className='d-flex justify-content-start'>
                                    <div className='rounded-circle p-2' style={{ width: 50, height: 50, backgroundColor: '#F6CC36', marginBottom: -20, zIndex: 1500 }}>
                                        <img src="assets/icons/Group (4).png" alt="" />
                                    </div>
                                </div>
                                <img src="assets/Frame 21 (2).png" width={'100%'} alt="" />
                                <div className='d-flex justify-content-end'>
                                    <div className='rounded-circle py-2 ps-1' style={{ width: 50, height: 50, backgroundColor: '#F92E5E', marginTop: -20, zIndex: 999 }}>
                                        <img src="assets/icons/Vector (4).png" alt="" />
                                    </div>
                                </div>

                            </Grid>
                        </Grid>
                    </Grid>

                </Grid>
            </Container>
        </div>
    )
}
