import { Container, Grid, Typography } from '@mui/material'
// import { Animation } from 'react-animate-style'
import React from 'react'
import { ContactUsContent } from '../components/contact-us/ContactUsContent'

export const ContactUs = () => {
    return (
        <div>
            <div className='contact-bg-img'>
                <Container maxWidth='xl'>
                    <Grid container direction={{ xs: 'row', md: 'row-reverse' }} display={'flex'} justifyContent={'end'} pt={8} pb={2}>
                        <Grid xs={12} md={4} display={'flex'} justifyContent={'end'} alignItems={'center'}>
                            {/* <Animation animationIn={'fadeInLeftBig'} isVisible={true} > */}
                            <img src="assets/drone-1.png" alt="" style={{ maxWidth: '100%', maxHeight: '250px' }} />
                            {/* </Animation> */}
                        </Grid>
                        <Grid xs={12} md={4} display={'flex'} justifyContent={{ xs: 'center', md: 'center' }} alignItems={'center'}>
                            {/* <Animation animationIn='wobble' isVisible animationInDelay={500}> */}
                            <Typography variant='h3' className='ff-mi' color={'white'} textAlign={'left'}>Contact Us</Typography>
                            {/* </Animation> */}
                        </Grid>
                    </Grid>
                </Container>
            </div>
            <ContactUsContent />
        </div>
    )
}
