import { Grid, Typography } from '@mui/material'
import React from 'react'

export const DashboardArealProject = () => {
  return (
    <div>
       <Grid marginTop={5}>
        <Typography className='ff-mo fw-bold' marginTop={'5'} color={'#1D69E7'}>AERIAL PROJECT</Typography>
        </Grid> 
        <Grid container display={'flex'} justifyContent={'space-between'} alignItems={'center'}>
        <Grid xs={12} md={5}>
        <Typography className='ff-mi' variant='h4' marginY={2}>New Technology and Feature Our Drone</Typography>
        <img src="assets/element-blur-2 1.png" alt="" className='blur-section-5' />
            </Grid>
            <Grid xs={12} md={5} sx={{marginTop:{xs:2,md:0}}}>
            <Typography className='ff-mo' color={'#656565'}>New advanced architecture with power efficient motor and high-performance battery</Typography>    
        </Grid> 
        </Grid>
          <Grid marginTop={5} container display={'flex'} justifyContent={'center'}>
              <Grid container xs={12} md={10}>
                  <Grid container xs={12} md={8}>
                      <Grid padding={1} xs={12} md={6}><img src="assets/Frame 4.png" width={'100%'} alt="" /></Grid>
                      <Grid padding={1} xs={12} md={6}><img src="assets/Frame 5.png" width={'100%'} alt="" /></Grid>
                      <Grid padding={1} xs={12}><img src="assets/Frame 7.png" width={'100%'} alt="" /></Grid>
                  </Grid>
                  <Grid padding={1} xs={12} md={4}>
                      <img src="assets/Frame 6.png" width={'100%'} alt="" />
                  </Grid>
              </Grid>
          </Grid>
    </div>
  )
}
